.richtext {
  background-color: var(--white);
  border: 1px solid var(--border);
  padding: 0.375rem 0.75rem;
  border-bottom-left-radius: 0.25rem;
  border-bottom-right-radius: 0.25rem;
  cursor: text;
}

.richtext:empty:before {
  content: attr(data-placeholder);
  color: #6c757d;
}

