[contenteditable] {
  outline: 0px solid transparent;
}

.richtext-formatting h1,
.richtext-formatting h2,
.richtext-formatting h3,
.richtext-formatting h4,
.richtext-formatting h5 {
  text-align: left;
  margin: 0;
  padding: 0;
}

.richtext-formatting h1 {
  font-size: 2.5rem;
}

.richtext-formatting h2 {
  font-size: 2.25rem;
}

.richtext-formatting h3 {
  font-size: 2rem;
}

.richtext-formatting h4 {
  font-size: 1.75rem;
}

.richtext-formatting h5 {
  font-size: 1.5rem;
}
