.alert-container {
  position: fixed;
  z-index: 9999;
  left: 50%;
  top: 16px;
}

/** 
* For xl-screens: Divide screen
*/
@media (min-width: 1200px) {
  .alert-container {
    margin-left: 8.33%;
  }
}

/** 
* For less than xl-screens: show pattern
*/
@media (max-width: 1200px) {
  .alert-container {
    margin-left: 25%;
  }
}

/** 
* For less than xl-screens: show pattern
*/
@media (max-width: 992px) {
  .alert-container {
    margin-left: 0;
  }
}
