/* 
* Start user theme 
*/
:root {
  --primary: #6c757d !important;
  --secondary: #e8f0fe !important;
  --light: #f8f9fa !important;
  --white: #fff !important;
  --dark: #6c757d !important;
  --gray-dark: #343a40 !important;
  --active: #f0f1f2 !important;
  --hover: #6c757d !important;
  --disabled: #e9ecef !important;
  --danger: #dc3545 !important;
  --info: #17a2b8 !important;
  --border: #ced4da !important;
}

* {
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  display: flex;
  /* background-color: var(--dark) !important; */
  /* overflow: hidden; */
  overflow-y: scroll;
  -webkit-font-smoothing: antialiased;
}

/* h1,
h2,
h3,
h4,
h5,
h6,
label,
span {
  font-weight: 500;
  font-family: "Fira Sans", sans-serif;
} */

body,
html,
.App,
#root,
.auth-wrapper {
  width: 100%;
  height: 100%;
}

.navbar-light {
  background-color: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}

@media (min-width: 768px) {
  .auth-wrapper {
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: left;
    padding-bottom: 5%;
  }
}

.auth-inner {
  max-width: 500px;
  width: 100%;
  margin: auto;
  /* background: #ffffff; */
  /* box-shadow: 0px 3px 10px rgba(34, 35, 58, 0.2); */
  padding: 40px 55px 45px 55px;
  /* border-radius: 15px; */
  /* border-radius: 0.25rem; */
  /* border: 1px solid rgba(0, 0, 0, 0.125); */
  border: 0;
  transition: all 0.3s;
}

.auth-wrapper .form-control:focus {
  border-color: #6c757d !important;
  border-color: var(--primary) !important;
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.other-options,
.other-options a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0;
}

/** 
* For xl-screens: Divide screen
*/
@media (min-width: 1200px) {
  body {
    background: linear-gradient(
      to right,
      #e8f0fe 33.33%,
      #f8f9fa 33.33%,
      #f8f9fa 83.33%,
      #e9ecef 83.33%,
      #e9ecef 100%
    );
    background: linear-gradient(
      to right,
      var(--secondary) 33.33%,
      var(--light) 33.33%,
      var(--light) 83.33%,
      var(--disabled) 83.33%,
      var(--disabled) 100%
    );
  }
}

/** 
* For less than xl-screens: show pattern
*/
@media (max-width: 1200px) {
  body {
    background: linear-gradient(
      to right,
      #e8f0fe 50%,
      #f8f9fa 50%
    );
    background: linear-gradient(
      to right,
      var(--secondary) 50%,
      var(--light) 50%
    );
  }
}

/** 
* For less than xl-screens: show pattern
*/
@media (max-width: 992px) {
  /* body {
    background: repeating-linear-gradient(
      to right,
      #f7f7f7,
      #f7f7f7 10px,
      #e8f6f3 10px,
      #e8f6f3 20px
    );
  } */
  body {
    background: #f8f9fa;
    background: var(--light);
  }
}

.btn:focus {
  outline: 3px solid #f8f9fa !important;
  outline: 3px solid var(--light) !important;
}

.btn-primary {
  color: #f8f9fa !important;
  color: var(--light) !important;
  background-color: #6c757d !important;
  background-color: var(--dark) !important;
  border-color: #6c757d !important;
  border-color: var(--dark) !important;
}

.btn-outline-primary {
  color: #6c757d !important;
  color: var(--dark) !important;
  background-color: var(--scondary) !important;
  border-color: #e8f0fe !important;
  border-color: var(--secondary) !important;
}

.btn-outline-primary:hover {
  /* color: var(--light) !important; */
  /* background-color: var(--dark) !important; */
  background-color: #f0f1f2 !important;
  background-color: var(--active) !important;

  border-color: #e8f0fe !important;

  border-color: var(--secondary) !important;
}

.list-group-item {
  color: #6c757d !important;
  color: var(--dark) !important;
}

.list-group-item.active {
  background-color: #f0f1f2 !important;
  background-color: var(--active) !important;
  border-color: #f0f1f2 !important;
  border-color: var(--active) !important;
}

a {
  color: #17a2b8 !important;
  color: var(--info) !important;
  font-weight: 400;
}

a.nav-link-2 {
  color: #f8f9fa !important;
  color: var(--light) !important;
}

a:hover {
  color: #343a40 !important;
  color: var(--gray-dark) !important;
}

a.nav-link-2:hover {
  color: #fff !important;
  color: var(--white) !important;
}

.bg {
  /* Full height */
  min-height: 100vh;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

.page-item.active .page-link {
  z-index: 3;
  color: #fff;
  background-color: #6c757d !important;
  background-color: var(--dark) !important;
  border-color: #6c757d !important;
  border-color: var(--dark) !important;
}

.button-transparent {
  background-color: Transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
  outline: none;
}

.no-focus:focus {
  outline: 0 !important;
}

input[readonly] {
  -webkit-touch-callout: none !important;
  -webkit-user-select: none !important;
  -moz-user-select: none !important;
  -ms-user-select: none !important;
  user-select: none !important;
}

.custom-control-label {
  font-weight: 400;
  cursor: pointer;
}

.custom-control-input:checked ~ .custom-control-label::before {
  color: #f8f9fa;
  color: var(--light);
  border-color: #6c757d !important;
  border-color: var(--dark) !important;
  background-color: #6c757d !important;
  background-color: var(--dark) !important;
}

.custom-control-input:disabled ~ .custom-control-label:before,
.custom-control-input[disabled] ~ .custom-control-label:before {
  border-color: #e9ecef !important;
  border-color: var(--disabled) !important;
  background-color: #e9ecef !important;
  background-color: var(--disabled) !important;
}

.variant-light .custom-control-label {
  color: #f8f9fa;
  color: var(--light);
}

.variant-light .custom-control-input ~ .custom-control-label::before {
  box-shadow: none;
  border-color: #f8f9fa !important;
  border-color: var(--light) !important;
}

.variant-light .custom-control-input:checked ~ .custom-control-label::before {
  color: #6c757d;
  color: var(--dark);
  background-color: #f8f9fa !important;
  background-color: var(--light) !important;
}

.variant-light .custom-control-input:disabled ~ .custom-control-label:before,
.variant-light .custom-control-input[disabled] ~ .custom-control-label:before {
  border-color: #e9ecef !important;
  border-color: var(--disabled) !important;
  background-color: #e9ecef !important;
  background-color: var(--disabled) !important;
}

/* Bullet of the switch control in variant light */
.variant-light .custom-control-input ~ .custom-control-label::after {
  background-color: #6c757d;
  background-color: var(--dark);
}
.variant-light .custom-control-input:checked ~ .custom-control-label::after {
  background-color: #6c757d;
  background-color: var(--dark);
}

.custom-range {
  color: #6c757d !important;
  color: var(--dark) !important;
  outline: none; /* Remove outline */
}

.custom-range::-webkit-slider-thumb {
  -webkit-appearance: none; /* Override default look */
  appearance: none;
  background: #6c757d !important;
  background: var(--dark) !important; /* Green background */
}

.custom-range::-moz-range-thumb {
  background: #6c757d !important;
  background: var(--dark) !important; /* Green background */
}

.link {
  cursor: pointer;
}

.invalid-feedback-custom {
  display: block;
  width: 100%;
  margin-top: 0.25rem;
  font-size: 80%;
  color: #dc3545;
}

