div.about {
  padding: 40px;
}

div.about h1 {
  text-align: center;
  letter-spacing: 0.02em;
  font-size: 2.3rem;
  font-weight: 400;
  line-height: 1.25;
}

.appetizer-about .appetizer-title {
  font-size: 1.25rem;
  line-height: 1.875rem;
  font-weight: 300;
  letter-spacing: 0.02rem;
  margin: 0 0 0.25rem 0;
  padding: 0;
}

.appetizer-about .appetizer-content,
.appetizer-about .appetizer-link {
  font-size: 0.9rem;
  margin: 0 0 0.25rem 0;
  padding: 0;
}

.appetizer-leadership .appetizer-title,
.appetizer-leadership .appetizer-content,
.appetizer-leadership .appetizer-link {
  margin: 0 !important;
  padding: 0 !important;
}

.appetizer-leadership .appetizer-content {
  color: black !important;
}
