div.media-inquiries {
  padding: 40px;
}

div.media-inquiries h1 {
  text-align: center;
  letter-spacing: 0.02em;
  font-size: 2.3rem;
  font-weight: 400;
  line-height: 1.25;
}

.appetizer-media-inquiries .appetizer-title,
.appetizer-regional-media-contacts .appetizer-title {
  font-size: 1.75rem;
  line-height: 1.2rem;
  font-weight: 400;
  line-height: 1.25;
  letter-spacing: 0.02rem;
  margin: 0 0 1.75rem 0;
  padding-top: 0;
  padding-bottom: 0;
}

.appetizer-regional-media-contacts .appetizer-content {
  margin: 0;
}
