}

.auth-inner-summary {
  width: 100%;
  max-width: 100%; /* 700px */
  margin: auto;
  /* background: #fbfcfc; */
  /* box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2); */
  /* padding: 1vh 1vw; */
  padding: 0;
  /* border-radius: 5px; */
